import * as React from 'react';
import Table from '../../../mo/models';
import { ITableSelection } from '../../types';
import SelectionRect from './selectionRect';
import { usePixelColsWidth } from '../../../utils/hooks/usePixelColsWidth';

interface TableHoverRectProps {
  node: Table;
  tableRef: React.RefObject<HTMLTableElement>;
  hoverSelection: ITableSelection | null;
  scale: number;
}

const TableHoverRect = (props: TableHoverRectProps) => {
  const { node, tableRef, hoverSelection, scale } = props;
  const [colsWidth] = usePixelColsWidth();

  return (
    <SelectionRect
      testid="table-hover-rect-wrapper"
      table={node}
      tableRef={tableRef}
      tableSelection={hoverSelection}
      scale={scale}
      colsWidth={colsWidth}
    />
  );
};

export default TableHoverRect;
